import React, { useState } from 'react'

import Calendar from 'react-calendar'

import styles from './date.filter.module.css'
import './react-calendar.css'
import arrowLeft from '../../../../../../assets/icons/Arrow-Left.png'

function DateFilterComponent(props) {
  const monthNames = [
    'Jan',
    'Feb',
    'Mar',
    'Apr',
    'May',
    'Jun',
    'Jul',
    'Aug',
    'Sept',
    'Oct',
    'Nov',
    'Dec',
  ]
  const dateTodayString =
    monthNames[new Date().getMonth()] +
    ' ' +
    new Date().getDate() +
    ', ' +
    new Date().getFullYear()
  const [id, setId] = useState(4)
  const [startDate, setStartDate] = useState(new Date())
  const [startDateString, setStartDateString] = useState(dateTodayString)
  const [endDate, setEndDate] = useState(new Date())
  const [endDateString, setEndDateString] = useState(dateTodayString)
  const [totalDays, setTotalDays] = useState(0)

  const selectDayRange = (id) => {
    setId(id)

    switch (id) {
      case 1:
        var oneDayAgo = new Date()
        oneDayAgo.setDate(oneDayAgo.getDate() - 1)
        setStartDate(oneDayAgo)
        calculateTotalDaysRange(oneDayAgo, new Date())
        setStartDateString(
          monthNames[oneDayAgo.getMonth()] +
            ' ' +
            oneDayAgo.getDate() +
            ', ' +
            oneDayAgo.getFullYear()
        )
        break
      case 2:
        var twoDaysAgo = new Date()
        twoDaysAgo.setDate(twoDaysAgo.getDate() - 2)
        setStartDate(twoDaysAgo)
        calculateTotalDaysRange(twoDaysAgo, new Date())
        setStartDateString(
          monthNames[twoDaysAgo.getMonth()] +
            ' ' +
            twoDaysAgo.getDate() +
            ', ' +
            twoDaysAgo.getFullYear()
        )
        break
      case 3:
        var threeDaysAgo = new Date()
        threeDaysAgo.setDate(threeDaysAgo.getDate() - 3)
        setStartDate(threeDaysAgo)
        calculateTotalDaysRange(threeDaysAgo, new Date())
        setStartDateString(
          monthNames[threeDaysAgo.getMonth()] +
            ' ' +
            threeDaysAgo.getDate() +
            ', ' +
            threeDaysAgo.getFullYear()
        )
        break
      default:
        setStartDate(new Date())
        calculateTotalDaysRange(new Date(), new Date())
        setStartDateString(
          monthNames[new Date().getMonth()] +
            ' ' +
            new Date().getDate() +
            ', ' +
            new Date().getFullYear()
        )
        break
    }
    setEndDateString(
      monthNames[new Date().getMonth()] +
        ' ' +
        new Date().getDate() +
        ', ' +
        new Date().getFullYear()
    )
    setEndDate(new Date())
  }

  const calculateTotalDaysRange = (start, end) => {
    const miliSecondsPerDay = 86400000
    const timeDifference = end.getTime() - start.getTime()
    const totalDays = Math.round(timeDifference / miliSecondsPerDay)
    setTotalDays(totalDays)
  }

  const selectStartDate = (day) => {
    setStartDate(day)
    setStartDateString(
      monthNames[day.getMonth()] +
        ' ' +
        day.getDate() +
        ', ' +
        day.getFullYear()
    )
    calculateTotalDaysRange(day, endDate)
  }

  const selectEndDate = (day) => {
    setEndDate(day)
    setEndDateString(
      monthNames[day.getMonth()] +
        ' ' +
        day.getDate() +
        ', ' +
        day.getFullYear()
    )
    calculateTotalDaysRange(startDate, day)
  }

  return (
    <div className="d-flex" style={{ height: '100%' }}>
      <div className={styles.options}>
        <div
          className={`${styles.optionItem} ${
            id === 1 ? styles.activeItem : null
          }`}
          onClick={() => selectDayRange(1)}
        >
          1 Day
        </div>
        <div
          className={`${styles.optionItem} ${
            id === 2 ? styles.activeItem : null
          }`}
          onClick={() => selectDayRange(2)}
        >
          Last 2 days
        </div>
        <div
          className={`${styles.optionItem} ${
            id === 3 ? styles.activeItem : null
          }`}
          onClick={() => selectDayRange(3)}
        >
          Last 3 days
        </div>
        <div
          className={`${styles.optionItem} ${
            id === 4 ? styles.activeItem : null
          }`}
          onClick={() => selectDayRange(4)}
        >
          Custom
        </div>
      </div>
      <div className={styles.dateSection}>
        <div className={styles.dateRangeDiv}>
          <p className={styles.dateRangeString}>
            {startDateString}{' '}
            <span>
              <img
                className={styles.arrowLeftIcon}
                src={arrowLeft}
                alt="ArrowLeftIcon"
              />
            </span>{' '}
            {endDateString}
          </p>
        </div>
        <div className={styles.totalDays}>
          {totalDays} Day(
          <small
            style={{ fontSize: '9px', marginLeft: '1px', marginRight: '1px' }}
          >
            s
          </small>
          )
        </div>
        <div className="d-flex">
          <div className={styles.startSection}>
            <Calendar
              onChange={setStartDate}
              onClickDay={selectStartDate}
              value={startDate}
            />
          </div>
          <div className={styles.divider}></div>
          <div className={styles.endSection}>
            <Calendar
              onChange={setEndDate}
              onClickDay={selectEndDate}
              value={endDate}
            />
          </div>
        </div>
        <div
          className="d-flex"
          style={{ marginTop: '-10px', marginLeft: '500px' }}
        >
          <p className={styles.clearFilterButton}>Clear</p>
          <button className={styles.applyFilterButton}>Apply</button>
        </div>
      </div>
    </div>
  )
}

export default DateFilterComponent
