import React, { useState } from 'react'
import useForceUpdate from 'use-force-update'

import styles from './monitoring.table.module.css'
import TableHeaderComponent from './monitoring-table-header/monitoring.table.header.component'
import TableBodyComponent from './monitoring-table-body/monitoring.table.body.component'
import TableFooterComponent from './monitoring-table-footer/monitoring.table.footer.component'
import DataViewComponent from './monitoring-table-body/monitoring-data-view/data-view.component'

function TableComponent() {
  const forceUpdate = useForceUpdate()
  const { useRef } = React
  const tableBodyRef = useRef()
  const [filterModel, setFilterModel] = useState({
    clients: undefined,
    titleFilter: undefined,
    titleFilterValue: undefined,
    workerFilter: undefined,
    workerFilterValue: undefined,
    durationFilter: undefined,
    durationFilterValue: undefined,
    status: undefined,
  })
  const [dataView, setDataView] = useState({})
  const [activeDataView, setActiveDataView] = useState(false)
  const [paginationData, setPaginationData] = useState({})

  const displayDataView = (data) => {
    setDataView(data)
    setActiveDataView(true)
  }

  const closeDataView = () => {
    setDataView({})
    setActiveDataView(false)
  }

  const eventOnDataLoaded = (data) => {
    setPaginationData(data)
  }

  const onChangedFilterModel = (key, value) => {
    setFilterModel((prevState) => ({
      ...prevState,
      [key]: value,
    }))
  }

  const onClickedButtonFilter = () => {
    tableBodyRef.current.getMonitoringData(
      filterModel,
      1,
      paginationData.pageSize
    )
  }

  const handleClick = () => {
    forceUpdate()
  }

  const onClickedClearFilter = (
    model = {
      clients: undefined,
      titleFilter: undefined,
      titleFilterValue: undefined,
      workerFilter: undefined,
      workerFilterValue: undefined,
      durationFilter: undefined,
      durationFilterValue: undefined,
      status: undefined,
    }
  ) => {
    setFilterModel(model)
    tableBodyRef.current.getMonitoringData(model, 1, paginationData.pageSize)
  }

  const paginate = (page, pageSize) => {
    tableBodyRef.current.getMonitoringData(filterModel, page, pageSize)
  }

  return (
    <div className="d-flex">
      <div>
        <div className={styles.table}>
          <div className={styles.tableDiv}>
            <table>
              <TableHeaderComponent
                onClickedButtonFilter={onClickedButtonFilter}
                onClickedClearFilter={onClickedClearFilter}
                onChangedFilterModel={onChangedFilterModel}
                activeDataView={activeDataView}
              />
              <TableBodyComponent
                ref={tableBodyRef}
                activeDataView={activeDataView}
                displayDataView={displayDataView}
                eventOnDataLoaded={eventOnDataLoaded}
              />
            </table>
          </div>
          <TableFooterComponent
            paginate={paginate}
            currentPage={paginationData.currentPage}
            pageSize={paginationData.pageSize}
            totalData={paginationData.totalCount}
            activeDataView={activeDataView}
          />
        </div>
      </div>
      <div
        style={{
          display: activeDataView ? null : 'none',
          paddingLeft: '10px',
        }}
      >
        <div className={styles.dataViewPanel}>
          <DataViewComponent closeDataView={closeDataView} data={dataView} />
        </div>
      </div>
    </div>
  )
}

export default TableComponent
