import React from 'react'

import styles from './user.table.module.css'

import UserTableBodyComponent from './user-table-body/user.table.body.component'
import UserTableFooterComponent from './user-table-footer/user.table.footer.component'
import UserTableHeaderComponent from './user-table-header/user.table.header.component'

function UserTableComponent() {
  return (
    <div className="d-flex">
      <div>
        <div className={styles.table}>
          <div className={styles.tableDiv}>
            <table>
              <UserTableHeaderComponent />
              <UserTableBodyComponent />
            </table>
          </div>
          <UserTableFooterComponent />
        </div>
      </div>
    </div>
  )
}

export default UserTableComponent
