import React, { useState } from 'react'

import styles from './monitoring.table.footer.module.css'
import chev from '../../../../assets/icons/Chevron-Right.png'
import chevLeftFirst from '../../../../assets/icons/Chev-Left-First.png'
import chevRightLast from '../../../../assets/icons/Chev-Right-Last.png'

function TableFooterComponent(props) {
  const [showPageSizeOptions, setShowPageSizeOptions] = useState(false)
  const pageSizeOptions = [12, 15, 50, 100]
  const totalPage = Math.trunc(props.totalData / props.pageSize)
  const firstPages = [1, 2, 3]
  const initialButtons = [1, 2, 3, 4, 5]
  const [middlePages, setMiddlePages] = useState([4, 5, 6, 7, 8])
  const lastPages = [totalPage - 2, totalPage - 1, totalPage]

  const paginateToFirstPage = () => {
    props.paginate(1, props.pageSize)
    paginateTo(1)
  }

  const paginateToLastPage = () => {
    props.paginate(totalPage, props.pageSize)
    paginateTo(totalPage)
  }

  const decrementPageButtonNumbers = () => {
    if (props.currentPage > 6 && props.currentPage < totalPage - 4) {
      setMiddlePages([
        middlePages[0] - 1,
        middlePages[1] - 1,
        middlePages[2] - 1,
        middlePages[3] - 1,
        middlePages[4] - 1,
      ])
    }
  }

  const paginateTo = (currentPage) => {
    if (currentPage < totalPage - 3 || currentPage > totalPage - 3) {
      setMiddlePages([
        (middlePages[0] = totalPage - 7),
        (middlePages[1] = totalPage - 6),
        (middlePages[2] = totalPage - 5),
        (middlePages[3] = totalPage - 4),
        (middlePages[4] = totalPage - 3),
      ])
    }
    if (currentPage > 6 && currentPage < totalPage - 4) {
      setMiddlePages([
        (middlePages[0] = currentPage - 2),
        (middlePages[1] = currentPage - 1),
        (middlePages[2] = currentPage),
        (middlePages[3] = currentPage + 1),
        (middlePages[4] = currentPage + 2),
      ])
    }
    if (currentPage <= 6) {
      setMiddlePages([
        (middlePages[0] = 4),
        (middlePages[1] = 5),
        (middlePages[2] = 6),
        (middlePages[3] = 7),
        (middlePages[4] = 8),
      ])
    }
  }

  const incrementPageButtonNumbers = () => {
    if (props.currentPage > 5 && props.currentPage < totalPage - 5) {
      setMiddlePages([
        middlePages[0] + 1,
        middlePages[1] + 1,
        middlePages[2] + 1,
        middlePages[3] + 1,
        middlePages[4] + 1,
      ])
    }
  }

  const togglePageSizeOptions = () => {
    setShowPageSizeOptions(!showPageSizeOptions)
  }

  const handlePreviousPage = () => {
    if (props.currentPage > 1) {
      props.paginate(props.currentPage - 1, props.pageSize)
    }
    decrementPageButtonNumbers()
  }

  const setCurrentPage = (page) => {
    props.paginate(page, props.pageSize)
    paginateTo(page)
  }

  const handleNextPage = () => {
    if (props.currentPage < props.totalData / props.pageSize) {
      props.paginate(props.currentPage + 1, props.pageSize)
    }
    incrementPageButtonNumbers()
  }

  const handlePageSize = (size) => {
    props.paginate(1, size)
    setShowPageSizeOptions(false)
  }

  return (
    <div className={styles.tableFooter}>
      <div className="row">
        <div className="col-12">
          <label className={styles.label}>Listing</label>
          <span className="position-relative">
            <button
              className={styles.listSelection}
              onClick={togglePageSizeOptions}
            >
              {props.pageSize ? props.pageSize : 0}
            </button>
            <div
              className={styles.pageSizeOptions}
              style={{ display: showPageSizeOptions ? null : 'none' }}
            >
              {pageSizeOptions.map((value, index) => {
                return (
                  <div
                    className={styles.optionItem}
                    key={index}
                    onClick={() => handlePageSize(value)}
                  >
                    {value}
                  </div>
                )
              })}
            </div>
          </span>
          <label className={styles.label}>
            entries, showing 1 to {props.pageSize} of {props.totalData}
          </label>

          <div
            className="btn-group float-end"
            role="group"
            style={{ display: totalPage > 1 ? null : 'none' }}
          >
            <button
              className={styles.paginationBtn}
              type="button"
              onClick={paginateToFirstPage}
            >
              <img
                className={styles.chevFirstPage}
                src={chevLeftFirst}
                alt="Paginate-Left-First"
              />
            </button>
            <button
              className={styles.paginationBtn}
              type="button"
              onClick={handlePreviousPage}
            >
              <img
                className={styles.chevLeftIcon}
                src={chev}
                alt="Paginate-Left-First"
              />
            </button>
            <>
              {props.totalData / props.pageSize > 5 ? (
                <>
                  {props.currentPage > 3 &&
                  props.currentPage < totalPage - 2 ? (
                    <>
                      <button
                        className={`${styles.paginationBtn} ${
                          1 === props.currentPage
                            ? styles.paginationActiveBtn
                            : null
                        }`}
                        type="button"
                        onClick={() => setCurrentPage(1)}
                      >
                        1
                      </button>
                      <button className={styles.paginationBtn} type="button">
                        ..
                      </button>
                      {middlePages.map((button, index) => {
                        return (
                          <button
                            className={`${styles.paginationBtn} ${
                              button === props.currentPage
                                ? styles.paginationActiveBtn
                                : null
                            }`}
                            type="button"
                            key={index}
                            onClick={() => setCurrentPage(button)}
                          >
                            {button}
                          </button>
                        )
                      })}
                      <button className={styles.paginationBtn} type="button">
                        ..
                      </button>
                      <button
                        className={`${styles.paginationBtn} ${
                          Math.trunc(props.totalData / props.pageSize) ===
                          props.currentPage
                            ? styles.paginationActiveBtn
                            : null
                        }`}
                        type="button"
                        onClick={() =>
                          setCurrentPage(
                            Math.trunc(props.totalData / props.pageSize)
                          )
                        }
                      >
                        {Math.trunc(props.totalData / props.pageSize)}
                      </button>
                    </>
                  ) : (
                    <>
                      {firstPages.map((button, index) => {
                        return (
                          <button
                            className={`${styles.paginationBtn} ${
                              button === props.currentPage
                                ? styles.paginationActiveBtn
                                : null
                            }`}
                            type="button"
                            key={index}
                            onClick={() => setCurrentPage(button)}
                          >
                            {button}
                          </button>
                        )
                      })}
                      <button className={styles.paginationBtn} type="button">
                        ..
                      </button>
                      {lastPages.map((button, index) => {
                        return (
                          <button
                            className={`${styles.paginationBtn} ${
                              button === props.currentPage
                                ? styles.paginationActiveBtn
                                : null
                            }`}
                            type="button"
                            key={index}
                            onClick={() => setCurrentPage(button)}
                          >
                            {button}
                          </button>
                        )
                      })}
                    </>
                  )}
                </>
              ) : (
                <>
                  {initialButtons.map((button, index) => {
                    return (
                      <button
                        className={`${styles.paginationBtn} ${
                          button === props.currentPage
                            ? styles.paginationActiveBtn
                            : null
                        }`}
                        type="button"
                        key={index}
                        onClick={() => setCurrentPage(button)}
                      >
                        {button}
                      </button>
                    )
                  })}
                </>
              )}
            </>
            <button
              className={styles.paginationBtn}
              type="button"
              onClick={handleNextPage}
            >
              <img
                className={styles.chevRightIcon}
                src={chev}
                alt="Paginate-Right-Last"
              />
            </button>
            <button
              className={styles.paginationBtn}
              type="button"
              onClick={paginateToLastPage}
            >
              <img
                className={styles.chevLastPage}
                src={chevRightLast}
                alt="Paginate-Right-Last"
              />
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default TableFooterComponent
