import React, { useState } from 'react'

import styles from './monitoring.table.header.module.css'
import filter from '../../../../assets/icons/Filter.png'
import arrowUpIcon from '../../../../assets/icons/Arrow-Up.png'
import clearFilterIcon from '../../../../assets/icons/Clear-Filter.png'

import ClientFilterComponent from './filters/client-filter/client-filter.component'
import StartsWithFilterComponent from './filters/starts-with-filter/starts-with-filter.component'
import DurationFilterComponent from './filters/duration-filter/duration-filter.component'
import StatusFilterComponent from './filters/status-filter/status-filter.component'
import DateFilterComponent from './filters/date-filter/date.filter.component'

function TableHeaderComponent(props) {
  const [showClientFilter, setShowClientFilter] = useState(false)
  const [showTitleFilter, setShowTitleFilter] = useState(false)
  const [showWorkerFilter, setShowWorkerFilter] = useState(false)
  const [showDurationFilter, setShowDurationFilter] = useState(false)
  const [showStatusFilter, setShowStatusFilter] = useState(false)
  const [showStartDateFilter, setShowStartDateFilter] = useState(false)
  const [showEndDateFilter, setShowEndDateFilter] = useState(false)

  // const removeClickEvent = (alt) => {
  //   if (alt === 'FilterIcon1')
  //     window.removeEventListener('click', toggleDropdown, true)
  // }

  const toggleDropdown = (e, header) => {
    // const alt = e.target.alt
    // removeClickEvent(alt)

    if (header === 'Client') {
      setShowClientFilter(true)
    } else if (header === 'Client-Close') {
      setShowClientFilter(false)
    } else {
      setShowClientFilter(false)
    }

    if (header === 'Title') {
      setShowTitleFilter(!showTitleFilter)
    } else {
      setShowTitleFilter(false)
    }

    if (header === 'Worker') {
      setShowWorkerFilter(!showWorkerFilter)
    } else {
      setShowWorkerFilter(false)
    }

    if (header === 'Start Date') {
      setShowStartDateFilter(!showStartDateFilter)
    } else {
      setShowStartDateFilter(false)
    }

    if (header === 'End Date') {
      setShowEndDateFilter(!showEndDateFilter)
    } else {
      setShowEndDateFilter(false)
    }

    if (header === 'Duration') {
      setShowDurationFilter(!showDurationFilter)
    } else {
      setShowDurationFilter(false)
    }

    if (header === 'Status') {
      setShowStatusFilter(!showStatusFilter)
    } else {
      setShowStatusFilter(false)
    }
  }

  return (
    <thead>
      <tr>
        <td className={styles.thClient}>
          CLIENT{' '}
          <img
            className={`${styles.filterIcons} mx-2`}
            src={filter}
            alt="FilterIcon1"
            value="1"
            onClick={(e) => toggleDropdown(e, 'Client')}
          />
          <div
            className={styles.dropdownClient}
            style={{ display: showClientFilter ? null : 'none' }}
          >
            {showClientFilter ? (
              <ClientFilterComponent
                show={showClientFilter}
                onChangedFilterModel={props.onChangedFilterModel}
                onClickedButtonFilter={props.onClickedButtonFilter}
                toggleDropdown={toggleDropdown}
              />
            ) : null}
          </div>
        </td>
        <td
          className={`${styles.thTitle} ${
            props.activeDataView ? styles.activeDataView : null
          }`}
        >
          TITLE{' '}
          <img
            className={`${styles.filterIcons} mx-2`}
            src={filter}
            alt="FilterIcon2"
            onClick={(e) => toggleDropdown(e, 'Title')}
          />
          <div
            className={styles.dropdownTitle}
            style={{ display: showTitleFilter ? null : 'none' }}
          >
            <StartsWithFilterComponent
              filterKey="titleFilter"
              filterValueKey="titleFilterValue"
              onChangedFilterModel={props.onChangedFilterModel}
              onClickedButtonFilter={props.onClickedButtonFilter}
              toggleDropdown={toggleDropdown}
            />
          </div>
        </td>
        <td
          className={`${styles.thWorker} ${
            props.activeDataView ? styles.activeDataView : null
          }`}
        >
          WORKER{' '}
          <img
            className={`${styles.filterIcons} mx-2`}
            src={filter}
            alt="FilterIcon3"
            onClick={(e) => toggleDropdown(e, 'Worker')}
          />
          <div
            className={styles.dropdownWorker}
            style={{ display: showWorkerFilter ? null : 'none' }}
          >
            <StartsWithFilterComponent
              filterKey="workerFilter"
              filterValueKey="workerFilterValue"
              onChangedFilterModel={props.onChangedFilterModel}
              onClickedButtonFilter={props.onClickedButtonFilter}
              toggleDropdown={toggleDropdown}
            />
          </div>
        </td>
        <td
          className={`${styles.thStarted} ${
            props.activeDataView ? styles.activeDataView : null
          }`}
        >
          STARTED{' '}
          <img
            className={`${styles.filterIcons} mx-2`}
            src={filter}
            alt="FilterIcon4"
            onClick={(e) => toggleDropdown(e, 'Start Date')}
          />
          <div
            className={styles.dropdownStartDate}
            style={{ display: showStartDateFilter ? null : 'none' }}
          >
            <DateFilterComponent
              onChangedFilterModel={props.onChangedFilterModel}
              onClickedButtonFilter={props.onClickedButtonFilter}
              toggleDropdown={toggleDropdown}
            />
          </div>
          <img src={arrowUpIcon} alt="ArrowUpIcon" />
        </td>
        <td
          className={`${styles.thEnded} ${
            props.activeDataView ? styles.activeDataView : null
          }`}
        >
          ENDED{' '}
          <img
            className={`${styles.filterIcons} mx-1`}
            src={filter}
            alt="FilterIcon5"
            onClick={(e) => toggleDropdown(e, 'End Date')}
          />
          <div
            className={styles.dropdownEndDate}
            style={{ display: showEndDateFilter ? null : 'none' }}
          >
            <DateFilterComponent
              onChangedFilterModel={props.onChangedFilterModel}
              onClickedButtonFilter={props.onClickedButtonFilter}
              toggleDropdown={toggleDropdown}
            />
          </div>
        </td>
        <td className={styles.thDuration}>
          DURATION{' '}
          <img
            className={`${styles.filterIcons} mx-2`}
            src={filter}
            alt="FilterIcon6"
            onClick={(e) => toggleDropdown(e, 'Duration')}
          />
          <div
            className={styles.dropdownDuration}
            style={{ display: showDurationFilter ? null : 'none' }}
          >
            <DurationFilterComponent
              onChangedFilterModel={props.onChangedFilterModel}
              onClickedButtonFilter={props.onClickedButtonFilter}
              toggleDropdown={toggleDropdown}
            />
          </div>
        </td>
        <td className={styles.thStatus}>
          STATUS{' '}
          <img
            className={`${styles.filterIcons} mx-2`}
            src={filter}
            alt="FilterIcon"
            onClick={(e) => toggleDropdown(e, 'Status')}
          />
          <img
            className={`${styles.clearFilterIcons} mx-2`}
            src={clearFilterIcon}
            alt="ClearFilterIcon"
            onClick={() => props.onClickedClearFilter()}
          />
          <div
            className={styles.dropdownStatus}
            style={{ display: showStatusFilter ? null : 'none' }}
          >
            <StatusFilterComponent
              onChangedFilterModel={props.onChangedFilterModel}
              onClickedButtonFilter={props.onClickedButtonFilter}
              toggleDropdown={toggleDropdown}
            />
          </div>
        </td>
      </tr>
    </thead>
  )
}

export default TableHeaderComponent
