import React, { useState, useEffect } from 'react'
import styles from './monitoring.table.body.module.css'

import MonitoringTableBodyService from './monitoring.table.body.service'

const { forwardRef, useImperativeHandle } = React
const TableBodyComponent = forwardRef((props, ref) => {
  const [selectedItem, setSelectedItem] = useState(0)
  const [data, setData] = useState([])
  const [isLoading, SetIsLoading] = useState(true)

  useEffect(() => {
    getAllWorkers()

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useImperativeHandle(ref, () => ({
    getMonitoringData(filterModel, pageNumber, pagSize) {
      getAllWorkers(filterModel, pageNumber, pagSize)
    },
  }))

  const getAllWorkers = (filterModel = {}, pageNumber = 1, pageSize = 50) => {
    SetIsLoading(true)
    var queryString = constructQueryString(filterModel, pageNumber, pageSize)

    MonitoringTableBodyService.getAllWorkers(queryString)
      .then((response) => {
        setData(response.data.workerResultItems)
        props.eventOnDataLoaded({
          totalCount: response.data.totalCount,
          pageSize: response.data.pageSize,
          currentPage: response.data.currentPage,
          totalPages: response.data.totalPages,
        })

        SetIsLoading(false)
      })
      .catch(() => {
        SetIsLoading(false)
      })
  }

  const constructQueryString = (filterModel, pageNumber, pageSize) => {
    const clientParam =
      filterModel.clients !== undefined ? '&client=' + filterModel.clients : ''
    var titleFilterParam =
      filterModel.titleFilter !== undefined
        ? '&titleFilter=' + filterModel.titleFilter
        : ''
    const titleFilterValueParam =
      filterModel.titleFilterValue !== undefined
        ? '&titleFilterValue=' + filterModel.titleFilterValue
        : ''
    var workerFilterParam =
      filterModel.workerFilter !== undefined
        ? '&workerFilter=' + filterModel.workerFilter
        : ''
    const workerFilterValueParam =
      filterModel.workerFilterValue !== undefined
        ? '&workerFilterValue=' + filterModel.workerFilterValue
        : ''
    var durationFilterParam =
      filterModel.durationFilter !== undefined
        ? '&durationFilter=' + filterModel.durationFilter
        : ''
    const durationFilterValueParam =
      filterModel.durationFilterValue !== undefined
        ? '&durationFilterValue=' + filterModel.durationFilterValue
        : ''
    const statusParam =
      filterModel.status !== undefined ? '&status=' + filterModel.status : ''

    let queryString =
      '?pageNumber=' +
      pageNumber +
      '&pageSize=' +
      pageSize +
      clientParam +
      titleFilterParam +
      titleFilterValueParam +
      workerFilterParam +
      workerFilterValueParam +
      durationFilterParam +
      durationFilterValueParam +
      statusParam

    return queryString
  }

  const selectItem = (id) => {
    setSelectedItem(data[id].jobId)
    props.displayDataView(data[id])
  }

  return (
    <>
      <tbody
        className={`${styles.tbody} ${styles.tbodyDiv}`}
        style={{ overflowY: data.length > 12 ? 'auto' : null }}
      >
        {isLoading === true ? (
          <div
            style={{
              fontSize: '50px',
              color: '#13ad83',
              backgroundColor: 'transparent',
              height: '100%',
              position: 'relative',
            }}
          >
            <i
              className="fas fa-circle-notch fa-spin mr-1"
              style={{
                top: 0,
                bottom: 0,
                left: 0,
                right: 0,
                margin: 'auto',
                position: 'absolute',
                height: '50px',
                width: '50px',
              }}
            ></i>
          </div>
        ) : (
          <>
            {data.length === 0 ? (
              <>
                <tr style={{ height: '100%' }}>
                  <td
                    colSpan="7"
                    style={{
                      fontSize: '50px',
                      color: '#13ad83',
                      textAlign: 'center',
                      backgroundColor: 'transparent',
                    }}
                  >
                    No Jobs Found
                  </td>
                </tr>
              </>
            ) : (
              <>
                {data.map((item, index) => {
                  const successClassName =
                    item.status === 'Success' ? styles.successStatus : null
                  const runningClassName =
                    item.status === 'Running' ? styles.runningStatus : null
                  const failedClassName =
                    item.status === 'Failed' ? styles.failedStatus : null
                  const stoppedClassName =
                    item.status === 'Stopped' ? styles.stoppedStatus : null
                  const queuedClassName =
                    item.status === 'Queued' ? styles.queuedStatus : null
                  return (
                    <tr
                      className={
                        selectedItem === item.jobId && props.activeDataView
                          ? styles.activeRow
                          : null
                      }
                      key={index}
                      onClick={() => selectItem(index)}
                      style={{ cursor: 'pointer' }}
                    >
                      <td className={styles.tdClient}>{item.client}</td>
                      <td
                        className={`${styles.tdTitle} ${
                          props.activeDataView ? styles.activeDataView : null
                        }`}
                      >
                        <div
                          style={{
                            whiteSpace: 'nowrap',
                            overflow: 'hidden',
                            width: '100%',
                          }}
                        >
                          {item.title}
                        </div>
                      </td>
                      <td
                        className={`${styles.tdWorker} ${
                          props.activeDataView ? styles.activeDataView : null
                        }`}
                      >
                        {item.worker}
                      </td>
                      <td
                        className={`${styles.tdStarted} ${
                          props.activeDataView ? styles.activeDataView : null
                        }`}
                      >
                        {item.started}
                      </td>
                      <td
                        className={`${styles.tdEnded} ${
                          props.activeDataView ? styles.activeDataView : null
                        }`}
                      >
                        {item.ended}
                      </td>
                      <td className={styles.tdDuration}>{item.duration}</td>
                      <td className={styles.tdStatus}>
                        <div
                          className={`${successClassName} ${runningClassName} ${failedClassName} ${stoppedClassName} ${queuedClassName}`}
                        >
                          {item.status}
                        </div>
                      </td>
                    </tr>
                  )
                })}
              </>
            )}
          </>
        )}
      </tbody>
    </>
  )
})

export default TableBodyComponent
