import React from 'react'

import styles from './user.table.footer.module.css'

function UserTableFooterComponent(props) {
  return (
    <div className={styles.tableFooter}>
      <div className="row">
        <div className="col-12">
          <label className={styles.label}>Listing</label>
          <span className="position-relative">
            <button className={styles.listSelection}>
              {props.pageSize ? props.pageSize : 0}
            </button>
            <div
              className={styles.pageSizeOptions}
              style={{ display: 'none' }}
            ></div>
          </span>
          <label className={styles.label}>
            entries, showing 0 to {props.pageSize} of{' '}
            {props.totalData ? props.totalData : 0}
          </label>

          <button className={styles.exportButton}>Export</button>

          {/* <div
            className="btn-group float-end"
            role="group"
            style={{ display: totalPage > 1 ? null : 'none' }}
          >
            <button
              className={styles.paginationBtn}
              type="button"
              onClick={paginateToFirstPage}
            >
              <img
                className={styles.chevFirstPage}
                src={chevLeftFirst}
                alt="Paginate-Left-First"
              />
            </button>
            <button
              className={styles.paginationBtn}
              type="button"
              onClick={handlePreviousPage}
            >
              <img
                className={styles.chevLeftIcon}
                src={chev}
                alt="Paginate-Left-First"
              />
            </button>
            <>
              {props.totalData / props.pageSize > 5 ? (
                <>
                  {props.currentPage > 3 &&
                  props.currentPage < totalPage - 2 ? (
                    <>
                      <button
                        className={`${styles.paginationBtn} ${
                          1 === props.currentPage
                            ? styles.paginationActiveBtn
                            : null
                        }`}
                        type="button"
                        onClick={() => setCurrentPage(1)}
                      >
                        1
                      </button>
                      <button className={styles.paginationBtn} type="button">
                        ..
                      </button>
                      {middlePages.map((button, index) => {
                        return (
                          <button
                            className={`${styles.paginationBtn} ${
                              button === props.currentPage
                                ? styles.paginationActiveBtn
                                : null
                            }`}
                            type="button"
                            key={index}
                            onClick={() => setCurrentPage(button)}
                          >
                            {button}
                          </button>
                        )
                      })}
                      <button className={styles.paginationBtn} type="button">
                        ..
                      </button>
                      <button
                        className={`${styles.paginationBtn} ${
                          Math.trunc(props.totalData / props.pageSize) ===
                          props.currentPage
                            ? styles.paginationActiveBtn
                            : null
                        }`}
                        type="button"
                        onClick={() =>
                          setCurrentPage(
                            Math.trunc(props.totalData / props.pageSize)
                          )
                        }
                      >
                        {Math.trunc(props.totalData / props.pageSize)}
                      </button>
                    </>
                  ) : (
                    <>
                      {firstPages.map((button, index) => {
                        return (
                          <button
                            className={`${styles.paginationBtn} ${
                              button === props.currentPage
                                ? styles.paginationActiveBtn
                                : null
                            }`}
                            type="button"
                            key={index}
                            onClick={() => setCurrentPage(button)}
                          >
                            {button}
                          </button>
                        )
                      })}
                      <button className={styles.paginationBtn} type="button">
                        ..
                      </button>
                      {lastPages.map((button, index) => {
                        return (
                          <button
                            className={`${styles.paginationBtn} ${
                              button === props.currentPage
                                ? styles.paginationActiveBtn
                                : null
                            }`}
                            type="button"
                            key={index}
                            onClick={() => setCurrentPage(button)}
                          >
                            {button}
                          </button>
                        )
                      })}
                    </>
                  )}
                </>
              ) : (
                <>
                  {initialButtons.map((button, index) => {
                    return (
                      <button
                        className={`${styles.paginationBtn} ${
                          button === props.currentPage
                            ? styles.paginationActiveBtn
                            : null
                        }`}
                        type="button"
                        key={index}
                        onClick={() => setCurrentPage(button)}
                      >
                        {button}
                      </button>
                    )
                  })}
                </>
              )}
            </>
            <button
              className={styles.paginationBtn}
              type="button"
              onClick={handleNextPage}
            >
              <img
                className={styles.chevRightIcon}
                src={chev}
                alt="Paginate-Right-Last"
              />
            </button>
            <button
              className={styles.paginationBtn}
              type="button"
              onClick={paginateToLastPage}
            >
              <img
                className={styles.chevLastPage}
                src={chevRightLast}
                alt="Paginate-Right-Last"
              />
            </button>
          </div> */}
        </div>
      </div>
    </div>
  )
}

export default UserTableFooterComponent
