import React, { useState } from 'react'

import styles from './users.module.css'

import AddUserViewComponent from './add-user-view/add.user.view.component'
import UserHeaderComponent from './user-header/user.header.component'
import UserTableComponent from './user-table/user.table.component'
import UserViewComponent from './user-view/user.view.component'

function UsersComponent() {
  const [showAddUserView, setShowAddUserView] = useState(false)
  const [showUserView, setShowUserView] = useState(false)

  const closeViewComponent = () => {
    setShowAddUserView(false)
    setShowUserView(false)
  }

  const toggleViewComponent = (component) => {
    if (component === 'add-user-view-component') {
      setShowAddUserView(!showAddUserView)
    } else {
      setShowAddUserView(false)
    }
  }

  return (
    <div>
      <UserHeaderComponent toggleViewComponent={toggleViewComponent} />
      <hr style={{ border: '1px inset #B1B1B1', marginBottom: '8px' }} />
      <div className="d-flex">
        <UserTableComponent toggleViewComponent={toggleViewComponent} />
        <div
          style={{
            display: showAddUserView || showUserView ? null : 'none',
            paddingLeft: '10px',
          }}
        >
          <div className={styles.dataViewPanel}>
            {showUserView ? (
              <UserViewComponent closeViewComponent={closeViewComponent} />
            ) : null}
            {showAddUserView ? (
              <AddUserViewComponent closeViewComponent={closeViewComponent} />
            ) : null}
          </div>
        </div>
      </div>
    </div>
  )
}

export default UsersComponent
