import React from 'react'

import styles from './user.table.header.module.css'
import filter from '../../../../../assets/icons/Filter.png'
import arrowUpIcon from '../../../../../assets/icons/Arrow-Up.png'
import clearFilterIcon from '../../../../../assets/icons/Clear-Filter.png'

function UserTableHeaderComponent(props) {
  return (
    <thead>
      <tr>
        <td className={styles.thFirstName}>
          FIRSTNAME{' '}
          <img
            className={`${styles.filterIcons} mx-2`}
            src={filter}
            alt="FilterIcon1"
          />
        </td>
        <td className={`${styles.thLastName}`}>
          LASTNAME{' '}
          <img
            className={`${styles.filterIcons} mx-2`}
            src={filter}
            alt="FilterIcon2"
          />
        </td>
        <td className={`${styles.thEmail}`}>
          EMAIL{' '}
          <img
            className={`${styles.filterIcons} mx-2`}
            src={filter}
            alt="FilterIcon3"
          />
        </td>
        <td className={`${styles.thRole} `}>
          ROLE{' '}
          <img
            className={`${styles.filterIcons} mx-2`}
            src={filter}
            alt="FilterIcon4"
          />
          <img src={arrowUpIcon} alt="ArrowUpIcon" />
        </td>
        <td className={`${styles.thDateCreated} `}>
          DATE CREATED{' '}
          <img
            className={`${styles.filterIcons} mx-1`}
            src={filter}
            alt="FilterIcon5"
          />
        </td>
        <td className={styles.thLastSeen}>
          LAST SEEN{' '}
          <img
            className={`${styles.filterIcons} mx-2`}
            src={filter}
            alt="FilterIcon6"
          />
          <img
            className={`${styles.clearFilterIcons} mx-2`}
            src={clearFilterIcon}
            alt="ClearFilterIcon"
          />
        </td>
      </tr>
    </thead>
  )
}

export default UserTableHeaderComponent
