import React, { useState } from 'react'

import styles from './add.user.view.module.css'
import defaultUserProfile from '../../../../assets/icons/Default-User-Profile.png'
import passwordKeyIcon from '../../../../assets/icons/Password-Key.png'
import circleCloseIcon from '../../../../assets/icons/Circle-Close.png'
import saveIcon from '../../../../assets/icons/Save.png'

function AddUserViewComponent(props) {
  const [passwordStrengthText, setPasswordStrengthText] = useState('too short')
  const [passwordStrength, setPasswordStrength] = useState(0)
  const [userModel, setUserModel] = useState({
    firstName: '',
    lastName: '',
    email: '',
    password: '',
    jobTitle: '',
    role: 0,
    client: 0,
  })

  const checkPasswordStrength = (passwordLength) => {
    if (passwordLength === 20) {
      setPasswordStrength(passwordStrength + 1)
      setPasswordStrengthText('strong')
    }
    if (passwordLength === 15) {
      setPasswordStrength(passwordStrength + 1)
      setPasswordStrengthText('medium')
    }
    if (passwordLength === 10) {
      setPasswordStrength(passwordStrength + 1)
      setPasswordStrengthText('good')
    }
    if (passwordLength === 5) {
      setPasswordStrength(passwordStrength + 1)
      setPasswordStrengthText('too short')
    }
  }

  const userModelChanged = (e) => {
    setUserModel((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }))

    if (e.target.name === 'password') {
      checkPasswordStrength(e.target.value.length)
    }
  }

  const postUser = () => {
    alert(JSON.stringify(userModel))
  }

  return (
    <>
      <div
        className={styles.closeButton}
        onClick={() => props.closeViewComponent()}
      ></div>
      <div
        className="d-flex position-relative"
        style={{ marginTop: '30px', width: '100%' }}
      >
        <p className={styles.title}>New User</p>
        <img
          className="position-absolute end-0"
          style={{ cursor: 'pointer ', marginRight: '-10px' }}
          src={defaultUserProfile}
          alt="DefaultUserProfile"
        />
      </div>
      <div style={{ width: '100%' }}>
        <div className={styles.inputForm}>
          <label className={styles.inputLabel}>First Name *</label>
          <br />
          <input
            className={styles.inputField}
            type="text"
            placeholder="Enter the user's first name"
            value={userModel.firstName}
            name="firstName"
            onChange={userModelChanged}
          />
        </div>
        <div className={styles.inputForm}>
          <label className={styles.inputLabel}>Last Name *</label>
          <br />
          <input
            className={styles.inputField}
            type="text"
            placeholder="Enter the user's last name"
            value={userModel.lastName}
            name="lastName"
            onChange={userModelChanged}
          />
        </div>
        <div className={styles.inputForm}>
          <label className={styles.inputLabel}>Email *</label>
          <br />
          <input
            className={styles.inputField}
            type="email"
            placeholder="Enter the user's email"
            value={userModel.email}
            name="email"
            onChange={userModelChanged}
          />
        </div>
        <div className={styles.inputForm}>
          <label className={styles.inputLabel}>
            Password *{' '}
            <img
              style={{ marginTop: '-3px', marginLeft: '5px' }}
              src={passwordKeyIcon}
              alt="PasswordKeyIcon"
            />
          </label>
          <br />
          <input
            className={styles.inputField}
            type="password"
            placeholder="Enter the user's password or generate one"
            value={userModel.password}
            name="password"
            onChange={userModelChanged}
          />
          <div className="d-flex">
            <div
              className={`${styles.inputFieldPasswordStrength1} ${
                passwordStrength > 0 ? styles.activePasswordStrength : null
              }`}
            ></div>
            <div
              className={`${styles.inputFieldPasswordStrength2} ${
                passwordStrength > 1 ? styles.activePasswordStrength : null
              }`}
            ></div>
            <div
              className={`${styles.inputFieldPasswordStrength3} ${
                passwordStrength > 2 ? styles.activePasswordStrength : null
              }`}
            ></div>
            <div
              className={`${styles.inputFieldPasswordStrength4} ${
                passwordStrength > 3 ? styles.activePasswordStrength : null
              }`}
            ></div>
          </div>
          <p className={styles.passwordStrengthText}>{passwordStrengthText}</p>
        </div>
        <div className={styles.inputForm} style={{ marginTop: '25px' }}>
          <label className={styles.inputLabel}>Confirm Password *</label>
          <br />
          <input
            className={styles.inputField}
            type="password"
            placeholder="Enter again the user's password"
          />
        </div>
        <div className={styles.inputForm}>
          <label className={styles.inputLabel}>Job Title</label>
          <br />
          <input
            className={styles.inputField}
            type="text"
            placeholder="Enter the user's job title"
            value={userModel.jobTitle}
            name="jobTitle"
            onChange={userModelChanged}
          />
        </div>
      </div>
      <div className="d-flex">
        <div className={styles.selectForm}>
          <label className={styles.selectLabel}>Role</label>
          <br />
          <button className={styles.selectButton}>Regular User</button>
        </div>
        <div className={styles.selectForm}>
          <label className={styles.selectLabel}>Client</label>
          <br />
          <button className={styles.selectButton}>Sky</button>
        </div>
      </div>
      <div className={styles.footer}>
        <img
          className={styles.saveIcon}
          src={saveIcon}
          alt="SaveIcon"
          onClick={postUser}
        />
        <img
          className={styles.circleCloseIcon}
          src={circleCloseIcon}
          alt="CircleCloseIcon"
        />
      </div>
    </>
  )
}

export default AddUserViewComponent
