import React from 'react'

import styles from './user.header.module.css'
import searchIcon from '../../../../assets/icons/Header-Search.png'

function UserHeaderComponent(props) {
  return (
    <div className={`${styles.vaiHeader} position-relative`}>
      <div>
        <b className={styles.vaiHeaderText}>Users</b>
      </div>
      <div className="d-flex position-absolute end-0">
        <div className="input-group" style={{ marginRight: '20px' }}>
          <input
            type="text"
            style={{ textIndent: '5px' }}
            className={styles.vaiHeaderSearch}
            placeholder="Search"
            aria-label="Search"
            aria-describedby="search-addon"
          />
          <div className={`${styles.vaiSearchIcon} input-group-append`}>
            <img src={searchIcon} alt="SearchIcon" />
          </div>
        </div>
        <div className="text-nowrap">
          <button
            className={styles.newUserAddButton}
            onClick={() => props.toggleViewComponent('add-user-view-component')}
          >
            New User
          </button>
        </div>
      </div>
    </div>
  )
}

export default UserHeaderComponent
